import io from "socket.io-client";

const state = {
    socket: null,
};

const getters = {
    socket: (state) => state.socket,
    socketID: (state) => state.socketID,
};

const actions = {
    connect({ commit }) {
        commit("connect");
    },
};

const mutations = {
    connect: (state) => {
        if (!state.socket) {
            var socket;
            if (process.env.NODE_ENV === "production") {
                socket = io();
            } else {
                socket = io("192.168.178.26:3001");
            }
            state.socketID = socket.id;
            state.socket = socket;
        } else {
            state.socket.socket.connect();
        }
    },
};

export default {
    state,
    getters,
    actions,
    mutations,
};
