<template>
  <div>
    <div class="page no-gutters">
      <div class="col">
        <div class="row mb-3">
          <div class="col">
            <button
              class="btn btn-secondary btn-block"
              v-bind:class="{disabled: wantToJoin==true}"
              v-on:click="setWantToJoin(false)"
            >Spiel erstellen</button>
          </div>
          <div class="col">
            <button
              class="btn btn-secondary btn-block"
              v-bind:class="{disabled: wantToJoin==false}"
              v-on:click="setWantToJoin(true)"
            >Spiel beitreten</button>
          </div>
        </div>
        <img src="./../assets/brunjo-trenner.svg" width="100%" alt />
        <p
          class="mb-4 font-weight-light"
        >W√§hle aus ob du ein Spiel erstellen oder einem beitreten m√∂chtest</p>

        <div class="row">
          <div class="col">
            <div class="form-group text-left">
              <label for="playerNameInput">Dein Nickname</label>
              <input
                type="text"
                maxlength="12"
                @input="checkFormToBeValid()"
                class="form-control"
                id="playerNameInput"
                v-model="playerName"
              />
            </div>
          </div>
          <div class="col" v-if="wantToJoin==true">
            <div class="form-group text-left">
              <label for="playerNameInput">Spielcode</label>
              <input
                type="number"
                @input="checkFormToBeValid()"
                class="form-control"
                id="playerNameInput"
                maxlength="12"
                v-model="gameCode"
              />
            </div>
          </div>
        </div>

        <button
          class="btn btn-primary btn-block btn-important"
          v-if="wantToJoin==true"
          :disabled="!this.valid"
          v-on:click="joinGame()"
        >Spiel beitreten</button>
        <button
          class="btn btn-primary btn-block btn-important"
          v-if="wantToJoin==false"
          :disabled="!this.valid"
          v-on:click="createGame()"
        >Spiel erstellen</button>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
export default {
  computed: mapGetters(["socket"]),
  created() {
    let uri = window.location.href.split("?");
    if (uri.length == 2) {
      let vars = uri[1].split("&");
      let getVars = {};
      let tmp = "";
      vars.forEach(function(v) {
        tmp = v.split("=");
        if (tmp.length == 2) getVars[tmp[0]] = tmp[1];
      });
      if (getVars.join != null) {
        this.gameCode = getVars.join;
        this.wantToJoin = true;
      }
    }
    document.addEventListener("keyup", e => {
      if (e.code === "Enter") {
        if ("activeElement" in document) document.activeElement.blur();
      }
    });
  },
  updated() {},
  methods: {
    setWantToJoin(wantToJoin) {
      this.wantToJoin = wantToJoin;
    },
    checkFormToBeValid() {
      if (!this.playerName || this.playerName.length > 12) {
        this.valid = false;
        return false;
      }
      if (this.wantToJoin) {
        if (!this.gameCode) {
          this.valid = false;
          return false;
        }
      }
      this.valid = true;
      return true;
    },
    createGame() {
      this.socket.emit("CreateGame", this.playerName);
    },
    joinGame() {
      this.socket.emit("JoinGame", {
        playerName: this.playerName,
        gameId: this.gameCode
      });
    }
  },
  data() {
    return {
      wantToJoin: false,
      valid: false,
      gameCode: null,
      playerName: ""
    };
  }
};
</script>

