const state = {
    connectedToGame: false,
};

const getters = {
    connectedToGame: (state) => state.connectedToGame,
};

const actions = {
    setConnectedToGame({ commit }, isConnected) {
        commit("setConnectedToGame", isConnected);
    },
};

const mutations = {
    setConnectedToGame: (state, isConnected) => {
        state.connectedToGame = isConnected;
    },
};

export default {
    state,
    getters,
    actions,
    mutations,
};
