import Vue from "vue";
import Vuex from "vuex";
import socketIOConnection from "./modules/socketIOConnection";
import gameConnectionState from "./modules/gameConnectionState";
import gameState from "./modules/gameState";

//Load Vuex
Vue.use(Vuex);

//Create Store
export default new Vuex.Store({
    modules: {
        socketIOConnection,
        gameConnectionState,
        gameState,
    },
});
