<template>
  <div class="page no-gutters">
    <div class="col">
      <br />
      <p class="text-center h3">{{gameInfo.round.questionText}}</p>
      <br />
      <br />
      <!-- Answering the Question -->
      <div class="row" v-if="!estimationAccepted">
        <div class="col">
          <button
            class="btn btn-primary btn-block h-100"
            v-bind:class="{disabled: answer!=1}"
            v-on:click="setAnswer(1)"
          >Ja</button>
        </div>
        <div class="col">
          <button
            class="btn btn-secondary btn-block h-100"
            v-bind:class="{disabled: answer!=2}"
            v-on:click="setAnswer(2)"
          >Nein</button>
        </div>
      </div>
      <br />
      <!-- End of Answering the Question -->

      <div v-if="!estimationAccepted">
        <p class="text-center">Sch√§tze wie viele deiner Mitspieler mit Ja antworten</p>
        <div class="row">
          <div class="col">
            <input
              type="number"
              class="form-control h-100"
              @blur="handleUpdate($event)"
              :max="gameInfo.playerList.length"
              :min="0"
              v-model="estimation"
            />
          </div>
          <div class="col">
            <button
              class="btn btn-primary btn-block h-100"
              :disabled="answer==0 || loading == true"
              v-on:click="sendAnswer()"
            >Sch√§tzung abgeben</button>
          </div>
        </div>
        <br />
      </div>

      <div v-if="answerAccepted && estimationAccepted">
        <p class="text-center h3">Deine Antwort ist gespeichert!</p>
        <p class="text-center">Warte auf deine Mitspieler...</p>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
export default {
  computed: mapGetters(["socket", "gameInfo", "iAmAdmin"]),
  mounted() {
    // Socket on √ºberschreibt nicht die Methode die dem Listener hinterlegt ist sonder f√ºgt einfach eine weitere Methode dem Listener hinzu.
    // Aus diesem Grund muss der Listener bei der destruction des components wieder entfernt werden, da sonst redundante funktionen hinzugef√ºgt werden
    // wenn der Component das n√§chste mal geladen wird.
    this.socket.on("SendAnswer", response => {
      if (response.state == 200) {
        console.log("Answer accepted");
        this.answerAccepted = true;
        this.sendEstimation();
      } else {
        this.loading = false;
      }
    });

    this.socket.on("SendEstimation", response => {
      if (response.state == 200) {
        this.estimationAccepted = true;
        this.loading = false;
      }
    });
  },
  destroyed() {
    this.socket.off("SendAnswer");
    this.socket.off("SendEstimation");
  },
  methods: {
    sendAnswer() {
      this.loading = true;
      console.log("Answer send");
      this.socket.emit("SendAnswer", this.answer);
    },
    sendEstimation() {
      this.socket.emit("SendEstimation", this.estimation);
    },
    resolveRound() {
      this.socket.emit("ResolveRound");
    },
    setAnswer(answer) {
      this.answer = answer;
    },
    handleUpdate(event) {
      if (event.target.value > event.target.max) {
        this.estimation = event.target.max;
      }
      if (event.target.value < event.target.min) {
        this.estimation = event.target.min;
      }
    }
  },
  data() {
    return {
      answerAccepted: false,
      estimationAccepted: false,
      estimation: 0,
      answer: 0,
      loading: false
    };
  }
};
</script>

