<template>
  <div id="app">
    <navbar />

    <div class="container">
      <GameHeader />

      <Home v-if="!connectedToGame" />
      <InGame v-if="connectedToGame" />

      <GameFooter />
    </div>
  </div>
</template>

<script>
import Navbar from "./components/Navbar.vue";
import Home from "./pages/Home.vue";
import InGame from "./pages/InGame/InGame";
import GameHeader from "./components/GameHeader";
import Vue from "vue";
import GameFooter from "./components/GameFooter";

import { mapActions, mapGetters } from "vuex";

export default {
  name: "App",
  components: {
    Navbar,
    Home,
    GameHeader,
    GameFooter,
    InGame
  },
  methods: {
    ...mapActions([
      "connect",
      "setConnectedToGame",
      "setGameInfo",
      "setIAmAdmin"
    ])
  },
  computed: mapGetters(["socket", "connectedToGame", "gameInfo"]),
  created() {
    this.connect();
    this.socket.on("CreateGame", data => {
      if (data.state == 200) {
        this.setGameInfo(data.gameInfo);
        this.setIAmAdmin(true);
        this.setConnectedToGame(true);
      } else {
        Vue.$toast.open({
          message: data.msg,
          type: "error",
          position: "bottom",
          duration: 4500
        });
      }
    });

    this.socket.on("JoinGame", data => {
      if (data.state == 200) {
        this.setGameInfo(data.gameInfo);
        this.setConnectedToGame(true);
      } else {
        Vue.$toast.open({
          message: data.msg,
          type: "error",
          position: "bottom",
          duration: 4500
        });
      }
    });

    this.socket.on("LeaveGame", data => {
      if (data.state == 200) {
        this.setGameInfo({});
        this.setConnectedToGame(false);
        this.setIAmAdmin(false);
        Vue.$toast.open({
          message: "Spiel Verlassen",
          type: "info",
          position: "bottom",
          duration: 4500
        });
      } else {
        Vue.$toast.open({
          message: data.msg,
          type: "error",
          position: "bottom",
          duration: 4500
        });
      }
    });

    this.socket.on("PingPong", data => {
      if (data.state != 200) {
        this.setGameInfo({});
        this.setConnectedToGame(false);
        this.setIAmAdmin(false);
        Vue.$toast.open({
          message: data.msg,
          type: "error",
          position: "bottom",
          duration: 4500
        });
      } else {
        setTimeout(() => {
          this.socket.emit("PingPong", this.gameInfo.id);
        }, 500);
      }
    });
    this.socket.on("YouAreAdmin", () => {
      Vue.$toast.open({
        message: "Du bist jetzt der Spieladmin",
        type: "info",
        position: "bottom",
        duration: 4500
      });
      this.setIAmAdmin(true);
    });
    this.socket.on("GameUpdate", data => {
      this.setGameInfo(data.gameInfo);
    });
  }
};
</script>

