<template>
  <div>
    <div class="row">
      <div class="col">
        <button class="btn btn-block btn-outline-secondary" v-on:click="leaveGame()">Spiel verlassen</button>
      </div>

      <div class="col">
        <button
          class="btn btn-block btn-success"
          id="copyLinkButton"
          v-on:click="copyInviteLink()"
        >Spielcode: {{this.gameInfo.id}}</button>
      </div>

      <div class="outer">
        <div class="inner">
          <input id="invite-link" v-model="this.inviteLink" />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
export default {
  computed: mapGetters(["gameInfo", "socket"]),
  methods: {
    generateInviteLinkt() {
      let uri = window.location.href.split("?")[0];
      uri = uri + "?join=" + this.gameInfo.id;
      return uri;
    },
    copyInviteLink() {
      this.inviteLink = this.generateInviteLinkt();
      var copyText = document.getElementById("invite-link");
      var copyLinkButton = document.getElementById("copyLinkButton");
      copyText.setAttribute("type", "text");
      copyText.setAttribute("value", this.inviteLink);

      /* Select the text field */
      copyText.select();
      copyText.setSelectionRange(0, 99999); /*For mobile devices*/

      /* Copy the text inside the text field */
      document.execCommand("copy");
      let oldButtonText = copyLinkButton.innerHTML;
      copyLinkButton.innerHTML = "Link kopiert";
      copyText.blur();
      setTimeout(function() {
        copyLinkButton.innerHTML = oldButtonText;
      }, 1000);
    },
    leaveGame() {
      if (confirm("Sicher dass du das Spiel verlassen willst?"))
        this.socket.emit("LeaveGame");
    }
  },
  data() {
    return {
      inviteLink: ""
    };
  }
};
</script>

<style scoped>
.outer {
  overflow: hidden;
  position: relative;
}
.inner {
  position: absolute;
  height: 100px;
  width: 100px;
  right: -50px;
  top: 50px;
}
</style>