const state = {
    gameInfo: {},
    iAmAdmin: false,
};

const getters = {
    gameInfo: (state) => state.gameInfo,
    iAmAdmin: (state) => state.iAmAdmin,
};

const actions = {
    setGameInfo({ commit }, gameInfo) {
        commit("setGameInfo", gameInfo);
    },
    setIAmAdmin({ commit }, iAmAdmin) {
        commit("setIAmAdmin", iAmAdmin);
    },
};

const mutations = {
    setGameInfo: (state, gameInfo) => {
        state.gameInfo = gameInfo;
    },
    setIAmAdmin: (state, iAmAdmin) => {
        state.iAmAdmin = iAmAdmin;
    },
};

export default {
    state,
    getters,
    actions,
    mutations,
};
