<template>
  <div class="page no-gutters">
    <div class="col text-left">
      <InGameHeader class="mb-4" />
      <div
        class="row no-gutters"
        v-if="this.gameInfo.resolveOfLastRound!=null && this.gameInfo.round==null"
      >
        <div class="col">
          <div class="my-3">
            <p class="text-center h3 mb-3">{{this.gameInfo.resolveOfLastRound.questionText}}</p>
            <br />
            <p
              class="text-center h4 mb-4"
              v-if="this.gameInfo.resolveOfLastRound.numberOfYes>1"
            >{{this.gameInfo.resolveOfLastRound.numberOfYes}} Spieler haben es gemacht</p>
            <p
              class="text-center h4 mb-4"
              v-else
            >{{this.gameInfo.resolveOfLastRound.numberOfYes}} Spieler hat es gemacht</p>
          </div>
          <br />
          <p class="text-left h6">Sch√§tzungen der Mitspieler:</p>
          <table class="table">
            <thead>
              <tr>
                <th scope="col">Spieler</th>
                <th scope="col">Sch√§tzung</th>
                <th scope="col">Differenz</th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="player in sortedPlayerList" v-bind:key="player.playerName">
                <td>{{player.playerName}}</td>
                <td>{{player.estimation}}</td>
                <td>{{player.difference}}</td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>

      <div class="mb-4" v-if="this.gameInfo.round==null">
        <div class="row">
          <div class="col">
            <button
              class="btn btn-block btn-primary btn-important"
              v-on:click="startRound()"
              :disabled="!iAmAdmin || gameInfo.playerList.length < 2"
            >Neue Runde</button>
          </div>
        </div>
        <p class="text-center" v-if="!iAmAdmin">Warte bis der Admin die Runde startet</p>
        <p
          class="text-center"
          v-if="gameInfo.playerList.length < 2"
        >Alleine spielen ist langweilig, lade noch Freunde ein ;)</p>
      </div>

      <InRound v-if="this.gameInfo.round!=null" class="mb-4" />

      <img src="./../../assets/brunjo-trenner.svg" class="my-3" width="100%" alt />
      <PlayerList />

      <div class="row no-gutters mt-4" v-if="iAmAdmin&&this.gameInfo.round!=null">
        <button class="btn btn-danger btn-block" v-on:click="resolveRound()">Runde beenden</button>
      </div>
    </div>
  </div>
</template>

<script>
import PlayerList from "./InGameComponents/PlayerList";
import InGameHeader from "./InGameComponents/InGameHeader";
import InRound from "./Round/InRound";
import { mapGetters } from "vuex";
export default {
  components: {
    PlayerList,
    InGameHeader,
    InRound
  },
  computed: {
    sortedPlayerList() {
      return this.gameInfo.resolveOfLastRound.playerList
        .slice(0)
        .sort((a, b) => (a.difference < b.difference ? -1 : 1));
    },
    ...mapGetters(["socket", "gameInfo", "iAmAdmin"])
  },
  methods: {
    startRound() {
      this.socket.emit("StartRound", { gameId: this.gameInfo.id });
    },
    resolveRound() {
      this.socket.emit("ResolveRound");
    }
  }
};
</script>

